body {
	background-color:#F7F7F7;
		background-image:url('/images/light_grey.png');

	font-family: 'Alegreya Sans', sans-serif;
}

.dopbcp-error {
	background-color:#F7F7F7;
}

p,li,span {
	font-size:16px;
	line-height:1.5em;
}

nav {
	background-color:#fff;
	padding:10px 0px;

	img {
		width:240px;
	}

	ul {
		float:right;
		margin-top:6px !important;
		a {
			color:#000;
			font-size:17px;
		}
	}
}

ul {
	margin-left:20px;
	padding-left:0;
}


footer {
	ul {
		padding:0;
		margin:0;
		li {
			display:inline;
		}
	}
}

.container-fluid .container {
	padding:0;
}


.mainImages {

	.right > img {
		height:200px;
		margin-bottom:20px;
	}
	img {
		width:100%;
	}
}


.homeFeatures {
	h2,h1 {
		font-size:29px;
		color:#D06856;
		//border-bottom:4px solid #fff;
	}

	.icons h4 {

		display:inline;
		float:left;
		margin-right:30px;

		i {
			margin-right:6px;
		}
	}
}

.images {
	background-color:#d06856;
	width:100%;
	margin-top:50px;
	padding:20px;


	h2 {
		color:#fff;
	}
}

.stripe {
	span {
		width:100%;
		float:left;
	}

	span:after {
		content:"*";
		color:red;
		margin-left:5px;
	}

	input {
		margin-bottom:15px;
	}

	label {

		span {
			font-size:13px;
		}
		color: #666666;
	    cursor: pointer;
	    display: block;
	    font-family: "Open Sans",sans-serif;
	    font-size: 11px !important;
	    font-style: italic;
	    font-weight: 300;
	    line-height: 20px;
	    margin: 0;
	}
}

.calendar {
	//background-color:#d06856;
	padding:20px;
	margin-top:50px;
}

#DOPBCPCalendar-no-items-wrapper1 {
	display:none;
}

.nav-tabs {
	padding:0;
	margin:0;
	margin-bottom:40px;
}

.delete-button {
	position:absolute;
	top:.2em;
	right:1em;
	font-weight:600;
	font-size:25px;
	color:#fff;
	text-shadow:0px 0px 4px rgba(0,0,0,.6);
	&:hover {
		cursor: pointer;
		text-decoration:none;
	}
}

form {

	textarea {
		margin-bottom:40px;
	}
}


input[type='submit'] {
	margin-top:50px;
}


.allPhotos {
	width:100%;
	> div {
		margin-bottom:30px;
		position:relative;
	}
	img {
		width:100%;
	}
}

footer {
	margin-top:30px;
	padding:10px 0px;
	background-color:#fff;
	width:100%;
}

.description {
	h2 {
		margin-top:0;
		padding-top:0px;
		margin-top:-10px;
	}
}

.m0 {
	margin:0 !important;
	margin-bottom:15px;
}